// ==========================================================================
// VARIABLES
// ========================================================================== */
$min-width: 320px;

// Media Breakpoints
$grid-breakpoints: (
  xs: 0,
  xss: 375px, // iPhone SE
  xsm: 390px, // iPhone 14
  xsl: 414px, // iPhone Plus/Max
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
) !default;

// Paths
$img: '../img';
$fonts: '../fonts';

// Fonts
/** Poppins **/
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');
$font-default: 'Poppins', sans-serif;

// Colors
$color-black: #191919;
$color-quartz: #4b4c51;
$color-grey: #9499a0;
$color-blueish: #aeb4bd;
$color-white: #f3f3f3;
$color-red: #ef2929;
$color-bordo: #bf0606;

// Transition Settings
$bezier: cubic-bezier(.645, .045, .355, 1);
$easing: $bezier;
$effect: .3s $easing;
$effect-s: .2s ease-in-out;

// SVG Icons
$icon-checkmark: 'data:image/svg+xml;base64,';
$icon-checkmark-white: 'data:image/svg+xml;base64,';

// Mixins
@mixin pseudo {content: '';display: block;overflow: hidden;}
@mixin clearfix {content: '';display: table;clear: both;}
@mixin user-select {-webkit-touch-callout: none;-webkit-user-select: none;-moz-user-select: none;-ms-user-select: none;user-select: none;}